  isActiveClaims: boolean = false;

  totalAssignedCount: number = 0;
  todayAssignedCount: number = 0;
  inprogressCount: number = 0;
  yetToStartCount: number = 0;
  userPermission: any;

  // statusTextDict = {
  //   'Draft': 'Draft',
