import { DatePipe } from '@angular/common';
import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router, ActivatedRoute, NavigationEnd, RouterEvent } from '@angular/router';
import { SeverityLevel } from '@microsoft/applicationinsights-web';
import { TranslateService } from '@ngx-translate/core';
import * as dayjs from 'dayjs';
import { Subscription, filter, firstValueFrom } from 'rxjs';
import { CLAIM_STATUS } from 'src/app/config/constants/claims.constants';
import { RegisteredGarageViewEstimateComponent } from 'src/app/dialogs/registered-garage-view-estimate/registered-garage-view-estimate.component';
import { RequestMissingInformationDialogComponent } from 'src/app/dialogs/request-missing-information-dialog/request-missing-information-dialog.component';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { claimMenuItems, menuItem } from 'src/app/model/menu';
import { ClaimsService } from 'src/app/services/claims/claims.service';
import { CommonService, i18nInfo } from 'src/app/services/common/common.service';
import { MonitorService } from 'src/app/services/monitor/monitor.service';
import { ChqTabsComponent } from 'src/app/widgets/chq-tabs/chq-tabs/chq-tabs.component';
import * as utc from 'dayjs/plugin/utc';
import { CoreService } from 'src/app/services/core/core.service';
dayjs.extend(utc)
@Component({
  selector: 'app-common-layout',
  templateUrl: './common-layout.component.html',
  styleUrls: [ './common-layout.component.scss' ]
})
export class CommonLayoutComponent implements OnInit, OnDestroy {

  claimMenuItems = claimMenuItems;
  claimId: string = '';
  objectId: number = 0; // this is actually claimId, due to unavailability of claimId variable name it's named as objectId
  @ViewChild('tabs') tabs: ChqTabsComponent;
  IsEstimateView: boolean = false;
  subscriptions: Subscription[] = [];
  roleName: string = '';
  claimStatus: string = '';
  fnolId: string = '';
  isEstimation: boolean = false;
  repairStatus: string = null;
  estimateAmount: number = 0;
  isThirdPartyExternalGarage: boolean = false;
  CLAIM_STATUS: any = CLAIM_STATUS
  showThirdPartyViewEstimateButton: boolean = false;
  showThirdPartyViewInvoiceButton: boolean = false;
  showInternalGarageViewInvoiceButton: boolean = false;
  hideRequestMissingInfoButton: boolean = false;
  requestForEnum: any = {
    requestForInvoice: 1,
    requestForEstimate: 2
  }
  public reqMissingInfoButtonModel: ChqWidgetsButtonModel = {
    label: 'request_missing_information',
    type: 'outline',
    icon: 'save-checked'
  }

  public i18n: i18nInfo;
  public claimDetails: any;
  public domainId: number;
  public claimNo: number;
  history: any[] = [];
  isHistoryLoading = false;
  showFraudDetected: boolean = false;
  fraudStatusText: string = 'No Fraud Detected';
  isDisabledRequestForEstimate: boolean = false;
  showNARegisteredGarage = true;
  /**
   * constructor
   * @param router
   */
  constructor(private router: Router,
    public dialog: MatDialog,
    private route: ActivatedRoute,
    private commonService: CommonService,
    private claimService: ClaimsService,
    private monitorService: MonitorService,
    private translateService: TranslateService,
    private coreService: CoreService,
    private datePipe: DatePipe) {
    this.i18n = this.commonService.geti18nInfo();
  }

  /**
   * on init
   */
  ngOnInit(): void {
    this.route?.params.subscribe((value) => {
      if (value && value['id']) {
        this.claimId = value['id'];
      }
    });

    if (location.search?.indexOf('readonly') !== -1) {
      this.commonService.isViewOnly = true;
    }

    if (location.pathname.indexOf('estimate') > -1) {
      this.IsEstimateView = true;
    }

    const subs = this.router.events
      .pipe(
        filter(event => event instanceof NavigationEnd)
      )
      .subscribe((event: RouterEvent) => {
        if (event.url.indexOf('estimate') > -1) {
          this.IsEstimateView = true;
        } else {
          this.IsEstimateView = false;
        }
      });

    const userPermission$ = this.commonService.userProfileData.subscribe((res) => {
      if (res?.data) {
        this.commonService.userId = res.data?.id;
        const automotiveServices = res?.data?.userPermission?.automotiveServices;
        const automotiveService = automotiveServices?.find(x => x.automotiveServiceName.toLowerCase() === 'addenda claims');
        const permissionList = automotiveService?.permissions;
        this.roleName = automotiveService?.roleName?.toLowerCase();
        this.commonService.roleName = this.roleName;

        const anyPermission: boolean = [ 'claim.selfapproval', 'claim.submitforapproval' ].some(itemP => permissionList?.map(item => item?.permissionName).includes(itemP));
        //if (this.roleName?.toLowerCase() != 'claim manager' && this.roleName?.toLowerCase() != 'claim reviewer') {
        // don't show garages except manager and reviewer
        if (!anyPermission) {
          this.claimMenuItems = this.claimMenuItems.filter(q => q.name != 'Garages')
        }

        const workFlowPackage = res.data.userPermission?.orgPackages?.filter((orgPackage: any) => {
          return orgPackage.automotiveServiceName === 'Addenda Claim Workflow';
        });

        if (workFlowPackage && workFlowPackage.length > 0) {
          const { permissions } = workFlowPackage[0];
          const stagePermission = permissions.filter((perm: any) => {
            return (perm.permissionName?.includes('1Stage') != -1
              || perm.permissionName?.includes('2Stage') != -1
              || perm.permissionName?.includes('3Stage') != -1);
          });
          if (stagePermission && stagePermission.length > 0) {
            this.commonService.claimWorkflowStage = stagePermission[0].permissionName;
          }
        }
        console.log(this.commonService.claimWorkflowStage);
      }
    });

    const claimStatus$ = this.commonService.claimStatus.subscribe((claimStatus) => {
      if (claimStatus) {
        this.claimStatus = claimStatus;
        this.isEstimation = !this.checkEstimate();
        this.showThirdPartyViewEstimateButton = this.thirdPartyViewEstimate();
        this.showThirdPartyViewInvoiceButton = this.thirdPartyViewInvoice();
        this.showNARegisteredGarage = this.registeredGarageApprovedCost();
        this.hideRequestMissingInfoButton = this.requestMissingButtonHide();
      }
    });

    const claimDetails$ = this.commonService.claimDetails.subscribe({
      next: (response) => {
        if (response) {
          this.fnolId = response.fnolId || '';
          this.commonService.fnolId = this.fnolId;
          this.claimDetails = response;
          this.claimStatus = response.claimStatus;
          this.repairStatus = response.garageDetails?.status ? response.garageDetails?.status : null;
          this.isDisabledRequestForEstimate = this.repairStatus === null || this.repairStatus === 'Cancelled';
          this.estimateAmount = response.garageDetails?.estimateAmount ? response.garageDetails?.estimateAmount : null;
          this.isEstimation = !this.checkEstimate();
          this.commonService.claimAssigneeId = response.assigneeId;
          this.commonService.claimCreatedId = response.createdUserId;
          this.isThirdPartyExternalGarage = response.garageDetails?.garageStatus === 'Registered';
          this.commonService.isThirdPartyExternalGarage = this.isThirdPartyExternalGarage;
          this.objectId = response.id;
          this.domainId = response.domainId;
          this.claimNo = response.claimNo;
          this.commonService.fraudStatus = response.fraudStatus;
          if (response.fraudStatus?.toLowerCase() === 'potential fraud detected') {
            this.showFraudDetected = true;
            this.fraudStatusText = 'Fraud Detected';
          }

          this.informUserToApproveClaim();
          this.showThirdPartyViewEstimateButton = this.thirdPartyViewEstimate();
          this.showThirdPartyViewInvoiceButton = this.thirdPartyViewInvoice();
          this.showInternalGarageViewInvoiceButton = this.internalViewInvoice();
          this.hideRequestMissingInfoButton = this.requestMissingButtonHide();

        }
      }
    })


    this.subscriptions.push(subs);
    this.subscriptions.push(userPermission$);
    this.subscriptions.push(claimDetails$);
    this.subscriptions.push(claimStatus$);
  }

  /**
   * informUserToApproveClaim
   */
  informUserToApproveClaim(): void {
    if (this.claimStatus === CLAIM_STATUS.EstimateApproved && (this.repairStatus.toLowerCase() === 'invoiced' || this.repairStatus.toLowerCase() === 'completed')) {
      const dialogRef = this.commonService.openConfirmYesNoDialogBig('Information', `CLaim #${this.claimNo} has an active invoice available. Please approve the claim to view the invoice.`)
      dialogRef.afterClosed().subscribe((action: boolean) => {
        if (action) {
          this.commonService.showLoading();
          this.claimService.updateClaimStatus(this.claimId, CLAIM_STATUS.SettlementInitiated, 'Settlement Initiated').subscribe({
            next: (response) => {
              if (response) {
                this.commonService.claimStatus.next(CLAIM_STATUS.SettlementInitiated);
                this.claimDetails.claimStatus = CLAIM_STATUS.SettlementInitiated;
                location.reload();
              }
              this.commonService.hideLoading();
            },
            error: () => {
              this.commonService.hideLoading();
            }
          });
        }
      });
    }
  }

  /**
   * navigate to selected route
   * @param item
   */
  navigate(item: menuItem): void {
    const queryParam = location.search;
    const url = item.url;
    const currentUrl = location.pathname;

    if (url === '/claim/claim-detail/{claimId}') {
      if ((queryParam.indexOf('mode=edit') !== -1)) {
        this.router.navigate([ url.replace('{claimId}', this.claimId || '') ], { queryParams: { mode: 'edit' } });
      } else {
        this.router.navigate([ url.replace('{claimId}', this.claimId || '') ]);
      }
    } else if (currentUrl === '/claim/claim-detail' || currentUrl === '/claim/claim-detail/0' || currentUrl === `/claim/claim-detail/${this.claimId}`) {
      if (url === '/claim/claim-detail/{claim-detail}' || currentUrl === '/claim/claim-detail/0') {
        return;
      } else {

        if ([ 'draft', 'claimauthenticated' ].includes(this.claimStatus?.toLowerCase())) {
          if (this.fnolId == '') {
            if ((url.toLowerCase().indexOf('document') != -1)
              || (url.toLowerCase().indexOf('photos') != -1)
              || (url.toLowerCase().indexOf('garage') != -1))
              return;
          }
        }

        this.tabs?.setActiveMenu(item);

        if ((queryParam.indexOf('mode=readonly') !== -1)) {
          this.commonService.isViewOnly = true;
          this.router.navigate([ url.replace('{claimId}', this.claimId || '') ], { queryParams: { mode: 'readonly' } });
        } else {
          this.router.navigate([ url.replace('{claimId}', this.claimId || '') ]);
        }
      }
    } else {

      if ([ 'draft', 'claimauthenticated' ].includes(this.claimStatus?.toLowerCase())) {
        if (this.fnolId == '') {
          if ((url.toLowerCase().indexOf('document') != -1)
            || (url.toLowerCase().indexOf('photos') != -1)
            || (url.toLowerCase().indexOf('garage') != -1))
            return;
        }
      }
      this.tabs?.setActiveMenu(item);
      if ((queryParam.indexOf('mode=edit') !== -1)) {
        this.router.navigate([ url.replace('{claimId}', this.claimId || '') ], { queryParams: { mode: 'edit' } });
      } else if ((queryParam.indexOf('mode=readonly') !== -1)) {
        this.commonService.isViewOnly = true;
        this.router.navigate([ url.replace('{claimId}', this.claimId || '') ], { queryParams: { mode: 'readonly' } });
      } else {
        this.router.navigate([ url.replace('{claimId}', this.claimId || '') ]);
      }

    }
  }

  /**
   * navigateToEstimate
   * 
   */
  navigateToEstimate(): void {
    this.router.navigate([ 'claim/claim-detail/' + this.claimId + '/estimate/' ]);
  }

  /**
   * requestFor
   * 
   */
  requestFor(action): void {
    this.monitorService.logEvent('requestFor', [ 'CommonLayoutComponent', 'addenda-claim', { action } ]);
    this.commonService.showLoading();
    firstValueFrom(this.claimService.requestForEstimate(this.claimId, action)).then((response) => {
      this.commonService.hideLoading();
      if (response) {
        this.commonService.showInfoToast(5000, response?.message);
      }
    }).catch((err) => {
      this.commonService.hideLoading();
      this.monitorService.logException(err, SeverityLevel.Error);
    });
  }

  /**
   * viewEstimateThirdPartyRegisteredGarage
   * 
   */
  viewEstimateThirdPartyRegisteredGarage(): void {
    this.monitorService.logEvent('viewEstimateThirdPartyRegisteredGarage', [ 'CommonLayoutComponent', 'addenda-claim' ]);
    this.commonService.showLoading();
    firstValueFrom(this.claimService.getEstimateThirdPartyRegisteredGarage(this.claimId)).then((response) => {
      this.commonService.hideLoading();
      if (response) {
        const dataToPass = []
        if (response?.data?.length > 0) {
          response.data.forEach((ele) => {
            if (ele.estimateReportName && ele.estimateReportUrl) {
              dataToPass.push({
                serialNo: dataToPass.length + 1,
                name: ele.estimateReportName,
                date: ele.createdDate ? dayjs.utc(ele.createdDate).local().format() : '-',
                url: ele.estimateReportUrl
              })
            }
            ele.documents.forEach((ele1) => {
              dataToPass.push({
                serialNo: dataToPass.length + 1,
                name: ele1.name,
                date: ele1.createdDate ? dayjs.utc(ele1.createdDate).local().format() : '-',
                url: ele1.documentUrl
              })
            })
          })
        }
        const dialogRef = this.dialog.open(RegisteredGarageViewEstimateComponent, {
          data: {
            list: dataToPass,
            title: this.translateService.instant('view_estimate'),
            showRejectEstimate: this.claimDetails?.claimStatus === CLAIM_STATUS.RepairEstimation
          },
          height: 'auto',
          width: '70%',
          autoFocus: true
        });
        dialogRef.afterClosed().subscribe((action: string | boolean) => {
          if (action === 'rejectEstimate') {
            const dialogRefReassign = this.commonService.openConfirmYesNoDialogBig('', this.translateService.instant('do_you_want_to_reassign_another_garage'), '', this.translateService.instant('yes'), this.translateService.instant('no'));
            dialogRefReassign.afterClosed().subscribe((action: boolean) => {
              if (action) {
                this.router.navigate([ `claim/claim-detail/${this.claimDetails.claimGuid}/garages` ], { queryParams: { reassign: 'true' } });
              } else {
                this.viewEstimateThirdPartyRegisteredGarage()
              }
            });
          }
        });
      }
    }).catch((err) => {
      this.commonService.hideLoading();
      this.monitorService.logException(err, SeverityLevel.Error);
    });
  }

  /**
   * viewEstimateThirdPartyRegisteredGarage
   * 
   */
  viewInvoiceThirdPartyRegisteredGarage(): void {
    this.monitorService.logEvent('viewInvoiceThirdPartyRegisteredGarage', [ 'CommonLayoutComponent', 'addenda-claim' ]);
    this.commonService.showLoading();

    if (this.claimStatus != CLAIM_STATUS.SettlementInitiated && this.claimStatus != CLAIM_STATUS.Complete) {
      this.claimService.updateClaimStatus(this.claimId, CLAIM_STATUS.SettlementInitiated).subscribe({
        next: (res) => {
          if (res) {
            this.commonService.showInfoToast(0, 'Status changed to ' + CLAIM_STATUS.SettlementInitiated);
            this.commonService.claimStatus.next(CLAIM_STATUS.SettlementInitiated);
            this.claimDetails.claimStatus = CLAIM_STATUS.SettlementInitiated;
            location.reload();
          }
        }, error: (err) => {
          this.commonService.hideLoading();
          this.monitorService.logException(err, SeverityLevel.Error);
        }
      })
    }

    firstValueFrom(this.claimService.getInvoiceThirdPartyRegisteredGarage(this.claimId)).then((response) => {
      this.commonService.hideLoading();
      if (response) {
        const dataToPass = []
        if (response.data) {
          if (response.data?.invoiceReportUrl) {
            dataToPass.push({
              serialNo: dataToPass.length + 1,
              name: 'Invoice Report',
              date: response.data.createdDate ? dayjs.utc(response.data.createdDate).local().format() : '-',
              url: response.data.invoiceReportUrl
            })
          }
          response.data?.documents.forEach((ele1) => {
            dataToPass.push({
              serialNo: dataToPass.length + 1,
              name: ele1.name,
              date: ele1.createdDate ? dayjs.utc(ele1.createdDate).local().format() : '-',
              url: ele1.blobUrl
            })
          })
        }
        this.openInvoiceTableDialog(dataToPass);
      }
    }).catch((err) => {
      this.commonService.hideLoading();
      this.monitorService.logException(err, SeverityLevel.Error);
    });

  }

  /**
   * viewInvoiceInternalGarage
   * 
   */
  viewInvoiceInternalGarage(): void {
    this.commonService.showLoading();
    this.monitorService.logEvent('viewInvoiceInternalGarage', [ 'CommonLayoutComponent', 'addenda-claim' ]);

    if (this.claimStatus != CLAIM_STATUS.SettlementInitiated && this.claimStatus != CLAIM_STATUS.Complete) {
      this.claimService.updateClaimStatus(this.claimId, CLAIM_STATUS.SettlementInitiated).subscribe({
        next: (res) => {
          if (res) {
            this.commonService.showInfoToast(0, 'Status changed to ' + CLAIM_STATUS.SettlementInitiated);
            this.commonService.claimStatus.next(CLAIM_STATUS.SettlementInitiated);
            this.claimDetails.claimStatus = CLAIM_STATUS.SettlementInitiated;
            location.reload();
          }
        }, error: (err) => {
          this.commonService.hideLoading();
          this.monitorService.logException(err, SeverityLevel.Error);
        }
      })
    }
    this.claimService.getInvoiceInternalGarage(this.claimDetails.id).subscribe({
      next: (response) => {
        this.commonService.hideLoading();
        const dataToPass = []
        if (response.data) {
          dataToPass.push({
            serialNo: dataToPass.length + 1,
            name: response.data.name,
            date: response.data.date ? dayjs.utc(response.data.date).local().format() : '-',
            url: response.data.blobUrl
          })
        }
        this.openInvoiceTableDialog(dataToPass);
      }, error: (err) => {
        this.commonService.hideLoading();
        this.monitorService.logException(err, SeverityLevel.Error);
      }
    })
  }

  /**
   * openInvoiceTableDialog()
   */
  openInvoiceTableDialog(data): void {

    this.dialog.open(RegisteredGarageViewEstimateComponent, {
      data: {
        list: data,
        title: this.translateService.instant('view_invoices')
      },
      height: 'auto',
      width: '70%',
      autoFocus: true
    });
  }



  /**
    Opens a dialog box for the missing information component with the specified data and panel class.
    @returns {void}
  */
  openReqMissingInfoPopup(): void {
    this.dialog.open(RequestMissingInformationDialogComponent, {
      data: null,
      height: 'auto',
      panelClass: 'operation-panel',
      autoFocus: false
    });
  }

  /**
   * @returns {void}
  */
  checkEstimate(): boolean {
    return [
      CLAIM_STATUS.Draft.toLowerCase(),
      CLAIM_STATUS.ClaimAuthenticated.toLowerCase(),
      CLAIM_STATUS.DetailsUpdated.toLowerCase(),
      CLAIM_STATUS.UploadDocuments.toLowerCase(),
      CLAIM_STATUS.UploadPhotos.toLowerCase(),
      CLAIM_STATUS.Submitted.toLowerCase(),
      CLAIM_STATUS.ReviewInProgress.toLowerCase(),
      CLAIM_STATUS.Assigned.toLowerCase(),
      CLAIM_STATUS.AssignedBack.toLowerCase(),
      CLAIM_STATUS.AssignedBackFromReview.toLowerCase(),
      CLAIM_STATUS.AssignedBackFromApproval.toLowerCase(),
      CLAIM_STATUS.Rejected.toLowerCase(),
      CLAIM_STATUS.ClaimSubmittedForApproval.toLowerCase(),
      CLAIM_STATUS.ApprovalReviewInProgress.toLowerCase(),
      CLAIM_STATUS.GarageAssignmentRequested.toLowerCase(),
      CLAIM_STATUS.GarageAssignmentRequestedRejected.toLowerCase(),
    ].includes(this.claimStatus?.toLowerCase()?.trim());
  }

  /**
   * @returns {void}
  */
  requestMissingButtonHide(): boolean {
    return [
      CLAIM_STATUS.Approved.toLowerCase(),
      CLAIM_STATUS.SettlementInitiated.toLowerCase(),
      CLAIM_STATUS.Complete.toLowerCase(),
    ].includes(this.claimStatus?.toLowerCase()?.trim());
  }
  /**
   * @returns {void}
  */
  thirdPartyViewEstimate(): boolean {
    return [
      CLAIM_STATUS.RepairEstimation.toLowerCase(),
      CLAIM_STATUS.Approved.toLowerCase(),
      CLAIM_STATUS.Complete.toLowerCase(),
      CLAIM_STATUS.RepairInvoice.toLowerCase(),
      CLAIM_STATUS.SettlementInitiated.toLocaleLowerCase(),
      CLAIM_STATUS.InvoiceRequested.toLocaleLowerCase()
    ].includes(this.claimStatus?.toLowerCase()?.trim());
  }
  /**
   * @returns {void}
  */
  thirdPartyViewInvoice(): boolean {
    return [
      CLAIM_STATUS.RepairInvoice.toLowerCase(),
      CLAIM_STATUS.Complete.toLowerCase(),
      CLAIM_STATUS.SettlementInitiated.toLocaleLowerCase(),
      CLAIM_STATUS.InvoiceRequested.toLocaleLowerCase()
    ].includes(this.claimStatus?.toLowerCase()?.trim());
  }

  /**
   * RegisteredGarageApprovedCost
   */
  registeredGarageApprovedCost(): boolean {
    return [
      CLAIM_STATUS.GarageAssignedforClaim.toLowerCase(),
      CLAIM_STATUS.GarageAssignmentRequestedRejected.toLowerCase(),
      CLAIM_STATUS.RepairEstimation.toLowerCase(),
      CLAIM_STATUS.EstimateRejected.toLowerCase(),
      CLAIM_STATUS.EstimateRequested.toLowerCase(),
    ].includes(this.claimStatus?.toLowerCase()?.trim());
  }

  /**
   * 
   */
  internalGarageApprovedCost(): boolean {
    return [
      CLAIM_STATUS.GarageAssignedforClaim.toLowerCase(),
      CLAIM_STATUS.GarageAssignmentRequestedRejected.toLowerCase(),
      CLAIM_STATUS.RepairEstimation.toLowerCase(),
      CLAIM_STATUS.EstimateRejected.toLowerCase(),
    ].includes(this.claimStatus?.toLowerCase()?.trim());
  }

  /**
   * internalViewInvoice
   * 
   */
  internalViewInvoice(): boolean {
    return [
      'invoiced',
      'complete',
    ].includes(this.repairStatus?.toLowerCase()?.trim())
      && [ CLAIM_STATUS.Approved, CLAIM_STATUS.InvoiceRequested, CLAIM_STATUS.SettlementInitiated, CLAIM_STATUS.Complete ].includes(this.claimStatus);
  }

  /**
   * on destroy
   */
  ngOnDestroy(): void {
    if (this.subscriptions) this.subscriptions.forEach((subscription: Subscription) => subscription.unsubscribe());
    this.commonService.userPermission.next(null);
    this.commonService.claimStatus.next(null);
    this.commonService.claimDetails.next(null);
  }

  /**
   * Get history
   */
  getHistory(): void {
    this.history = [];
    this.isHistoryLoading = true;
    this.coreService.getHistory(this.objectId, this.domainId).subscribe({
      next: (response) => {
        if (response) {
          let allHistoryData = [];
          for (let i = 0; i < response.length; i++) {
            allHistoryData = [ ...allHistoryData, ...response[i].auditTrailResponseDetails ]
          }
          const data = allHistoryData.map(x => ({
            ...x,
            auditDate: this.datePipe.transform(x.actionedOn, 'dd/MM/YYYY')
          }));
          const mergedHistory = [];
          for (let i = 0; i < data.length; i++) {
            const isExist = mergedHistory.find(x => x.auditDate === data[i].auditDate);
            if (isExist) {
              isExist['auditTrailResponseDetails'].push(data[i]);
            } else {
              const obj = {
                auditDate: data[i].auditDate,
                claimNo: this.claimNo,
                auditTrailResponseDetails: [ data[i] ]
              }
              mergedHistory.push(obj);
            }
          }
          this.history = mergedHistory;
        }
      },
      complete: () => {
        this.isHistoryLoading = false
      }
    });
  }
}
