import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { MatSidenav } from '@angular/material/sidenav';
import { Router } from '@angular/router';
import * as dayjs from 'dayjs';
import { forkJoin, map } from 'rxjs';
import { CLAIM_STATUS, STATUS_TEXT_DICT } from 'src/app/config/constants/claims.constants';
import { ClaimDashboardTableConfig } from 'src/app/config/display/claims-table-config';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { ChqWidgetsInputModel } from 'src/app/model/chq-widgets-input-model';
import { menuItem } from 'src/app/model/menu';
import { CaseService } from 'src/app/services/case/case.service';
import { ClaimsService } from 'src/app/services/claims/claims.service';
import { CommonService, i18nInfo } from 'src/app/services/common/common.service';
import { ChqChartComponent } from 'src/app/widgets/chq-chart/chq-chart.component';
import { CellClickedCallback, TableCellData, TableConfig, TableRowData } from 'src/app/widgets/xa-table/xa-table.component';
import { ChartOptions } from '../../home/dashboard/dashboard.component';

const colorsClaimStatus = {
  'Approved': '#0FCA7A',
  'UnApproved': '#00C7F2',
  'Others': '#F7A23B'
}

const colorsApprovedRejected = {
  'Approved': '#00C7F2',
  'Rejected': '#F75D5F',
  'Others': '#F7A23B'
}

@Component({
  selector: 'app-claim-dashboard',
  templateUrl: './claim-dashboard.component.html',
  styleUrls: [ './claim-dashboard.component.scss' ]
})
export class ClaimDashboardComponent implements OnInit {

  public dashboardMenuItems: menuItem[] = [
    {
      name: 'KPI Dashboard',
      displayName: 'KPI Dashboard',
      icon: '',
      url: '',
      title: '',
      subTitle: ''
    }
  ];

  //filter
  filterDataLoaded = false;
  public userDetail = null;
  @ViewChild('filter') public filter: MatSidenav;
  public filterForm = this.formBuilder.group({
    org: [],
    policyCoverType: [],
  });
  filterModal: ChqWidgetsInputModel[] = [
    {
      placeHolder: 'select_type',
      label: 'organization',
      name: 'org',
      value: '',
      type: 'select',
      displayValue: 'label',
      fieldValue: 'value',
      options: [],
    },
    {
      placeHolder: 'All',
      name: 'policyCoverType',
      label: 'policy_cover_type',
      value: '',
      type: 'select',
      displayValue: 'label',
      fieldValue: 'value',
      options: []
    },

  ];

  public creationStartDateTextBox: ChqWidgetsInputModel = {
    placeHolder: 'creation_date_from',
    label: 'creation_date_from',
    name: 'fromDate',
    value: dayjs().subtract(6, 'month').toISOString(),
    maxDate: dayjs().toDate(),
    minDate: dayjs().subtract(1, 'year').toDate(),
    type: 'date'
  }

  public creationEndDateTextBox: ChqWidgetsInputModel = {
    placeHolder: 'creation_date_to',
    label: 'creation_date_to',
    name: 'toDate',
    value: dayjs().toISOString(),
    minDate: dayjs().subtract(1, 'year').toDate(),
    maxDate: dayjs().toDate(),
    type: 'date'
  }

  public applyButton: ChqWidgetsButtonModel = {
    label: 'apply',
    type: 'primary'
  }

  public resetButton: ChqWidgetsButtonModel = {
    label: 'reset_all',
    type: 'outline'
  }

  //
  public isSummaryLoading: boolean = false;
  public fnolChartOptions: Partial<ChartOptions>;
  public fnolApprovedRejectedOptions: Partial<ChartOptions>;
  public fnolFraudDetectedOptions: Partial<ChartOptions>;

  public fnolDetails: any;
  public claimDetails: any;

  public tableConfig: TableConfig;
  public tableData: any;
  i18n: i18nInfo;

  private organizationId: string;

  private readonly configData: any = ClaimDashboardTableConfig;

  private fraudDetectPercentage: number;

  public dashboardData: any;

  public fileClaimButton: ChqWidgetsButtonModel = {
    label: 'file_a_claim',
    type: 'primary',
    icon: 'file-claim',
    iconColor: 'white',
    onclick: this.fileClaim.bind(this)
  }

  donotChartOptions = (): any => {
    return {
      series: [],
      chart: {
        type: 'donut',
        height: 250,
      },
      labels: [],
      responsive: [
        {
          breakpoint: 480,
          options: {
            chart: {
              width: 200
            },
            legend: {
              position: 'bottom'
            }
          }
        }
      ],
      colors: [],
      dataLabels: {
        enabled: true,
        style: {
          colors: [ '#fff' ],
          fontSize: '8px',
        },
        formatter: function (val: number): string {
          return val.toFixed(2) + '%'
        },
      },
      stroke: {
        width: 0,
      },
      plotOptions: {
        pie: {
          donut: {
            labels: {
              show: false
            }
          }
        }
      },
      states: {
        hover: {
          filter: {
            type: 'none',
          }
        },
        active: {
          filter: {
            type: 'darken',
            value: 0.75,
          }
        }

      },
      legend: {
        position: 'bottom',
        horizontalAlign: 'left',
      },
      tooltip: {
        enabled: true,
        y: {
          formatter: function (val: number): string {
            return val.toFixed(2) + '%'
          }
        }
      }
    }
  };

  @ViewChild('chart') chart: ChqChartComponent;
  /**
   * constructor 
   */
  constructor(
    private commonService: CommonService,
    private router: Router,
    private claimService: ClaimsService,
    private caseService: CaseService,
    private formBuilder: FormBuilder) {
    this.fnolChartOptions = this.donotChartOptions();
    this.fnolApprovedRejectedOptions = this.donotChartOptions();
    this.fnolFraudDetectedOptions = this.donotChartOptions();
    this.i18n = this.commonService.geti18nInfo();

    this.commonService.userProfileData.subscribe((res) => {
      this.userDetail = res?.data;

      if (this.userDetail) {
        this.organizationId = this.userDetail.organizationDetail?.id;
      }
      
    });
  }

  /**
   * ng on init
   */
  ngOnInit(): void {
    //this.isSummaryLoading = true;
    this.tableConfig = this.configData;
    this.getClaims();
    this.getDashboardData();
  }


  /**
   * get dashboard data
   */
  getDashboardData(payload?: any): void {

    if(!payload) {
      payload = {
        'orgnization': this.organizationId?.toString(),
        'startDate': null,
        'endDate': null,
        'policyType': ''
      }
    }
  
    this.claimService.getDashboardData(payload).subscribe({
      next: (resp: any) => {
        console.log(resp);

        if (resp?.data) {
          this.dashboardData = resp.data;
          this.fnolDetails = this.dashboardData?.fNOLDetail;
          this.claimDetails = this.dashboardData?.claimDetail;

          this.getDashboardClaimApprovalRateInformation();
          this.getDashboardFNOLApprovedRejectedInformation();
          this.getDashboardFNOLFraudDetectionInformation();
        }
        this.commonService.hideLoading();

      }, error: () => {
        this.commonService.hideLoading();
      }
    });
  }

  /**
   * file a claim
   */
  fileClaim(): void {
    this.router.navigate([ 'claim/claim-detail/0' ]);
  }


  /**
   * navigate to other tab
   * @param $event 
   */
  navigate($event): void {
    //navigate to other tab
  }

  /**
   * getClaims
   */
  getClaims(): void {
    this.commonService.showLoading();

    this.claimService.getTopFiveClaimList().subscribe({
      next: (resp: any) => {
        this.tableData = resp?.value.map((x) => {
          x.Status = STATUS_TEXT_DICT[x.Status];
          return x;
        })
        this.commonService.hideLoading();

      }, error: () => {
        this.commonService.hideLoading();
      }
    });
  }

  /**
    Retrieves dashboard information.
    @returns {void}
  */
  getDashboardClaimApprovalRateInformation(): void {
    const data = this.dashboardData.claimapproveRejectDetail;

    const claimapproveRejectDetail = [];
    Object.keys(data).forEach((element) => {

      let status = '';
      if (element === 'approvePercentage') {
        status = CLAIM_STATUS.Approved;
      } else if (element === 'unApprovePercentage') {
        status = 'UnApproved';
      } else {
        status = 'Others';
      }

      claimapproveRejectDetail.push({
        claimStatusPercentage: data[element],
        claimStatusName: status
      });
    });

    this.fnolChartOptions = {
      ...this.fnolChartOptions,
      series: claimapproveRejectDetail.map(ele => ele.claimStatusPercentage),
      labels: claimapproveRejectDetail.map(ele => ele.claimStatusName),
      colors: claimapproveRejectDetail.map(ele => colorsClaimStatus[ele.claimStatusName] || '#000')
    }
  }

  /**
   * get FNOL approved rejected information
   */
  getDashboardFNOLApprovedRejectedInformation(): void {

    const data = this.dashboardData.fNOLapproveRejectDetail;

    const fnolapproveRejectDetail = [];
    Object.keys(data).forEach((element) => {

      let status = '';
      if (element === 'approvePercentage') {
        status = CLAIM_STATUS.Approved;
      } else if (element === 'otherPercentage') {
        status = 'Others';
      } else {
        status = 'Rejected';
      }

      fnolapproveRejectDetail.push({
        fnolApprovalRejectionPercentage: data[element],
        //fnolApprovalRejectionPercentage: status === CLAIM_STATUS.Approved ? 60 : 40,
        fnolStatus: status
      });
    });

    this.fnolApprovedRejectedOptions = {
      ...this.fnolApprovedRejectedOptions,
      series: fnolapproveRejectDetail.map(ele => ele.fnolApprovalRejectionPercentage),
      labels: fnolapproveRejectDetail.map(ele => ele.fnolStatus),
      colors: fnolapproveRejectDetail.map(ele => colorsApprovedRejected[ele.fnolStatus] || '#000')
    }
  }

  /**
   * get FNOL Fraud detection information 
   */
  getDashboardFNOLFraudDetectionInformation(): void {

    const data = this.dashboardData.claimFraudDetail;

    const claimFraudDetail = [];
    Object.keys(data).forEach((element) => {

      let status = '';
      let color = '';
      if (element === 'detected') {
        status = 'Fraud Detected';
        color = '#F7A23B';
      } else {
        status = 'Total Cases Received';
        color = '#5598EB';
      }


      claimFraudDetail.push({
        fraudDetectPercent: data[element],
        label: status,
        color: color
      });
    });
    this.fnolFraudDetectedOptions = {
      ...this.fnolFraudDetectedOptions,
      series: claimFraudDetail.map(ele => ele.fraudDetectPercent),
      labels: claimFraudDetail.map(ele => ele.label),
      colors: claimFraudDetail.map(ele => ele.color || '#000')
    }
  }

  /**
   * handle cell click 
   * @param row 
   * @param column 
   * @param event 
   * @returns 
   */
  handleCellClick: CellClickedCallback = (row: TableRowData, column: Partial<TableCellData>): void => {
    if (column.id !== 'ClaimNo') {
      return;
    }

    const claimId = row.find(c => c.id === 'ClaimGuid')?.result?.value;
    this.router.navigate([ 'claim/claim-detail/' + claimId + '/customer-view' ]);
  }

  /**
   * open filter
   */
  openFilter(): void {
    if (!this.filterDataLoaded) {

      this.commonService.showLoading();

      forkJoin([
        this.caseService.getOrganizations(),
        this.claimService.getClaimLookups('CLMPolicyType')
      ]
      ).pipe(
        map(([ orgList, policyTypes ]) => {
          return { orgList, policyTypes };
        })
      ).subscribe((result) => {

        this.filterModal[0] =
        {
          ...this.filterModal[0],
          selectedOption: { 'label': this.userDetail?.organizationDetail.name, 'value': this.userDetail?.organizationDetail.id },
          options: result.orgList?.data.map((x) => { return { label: x.name, value: x.id } })
        };

        this.filterModal[1] =
        {
          ...this.filterModal[1],
          options: result.policyTypes?.data.map((x) => { return { label: x.name, value: x.id } })
        };

        this.filterDataLoaded = true;
        this.commonService.hideLoading();
        this.filter.open();
      });

    } else {
      this.filter.open();
    }
  }


  /**
  * onFormUpdate
  */
  onFormUpdate(output: any): void {
    if (output.name === 'org') {
      this.filterModal[0] = { ...this.filterModal[0], selectedOption: output.value };
      if (output.value) {
        this.filterForm.get(output.name).setValue(output.value?.value);
      } 
    } else if (output.name === 'policyCoverType') {
      this.filterModal[1] = { ...this.filterModal[1], selectedOption: output.value };
      this.filterForm.get(output.name).setValue(output.value?.label);
    } else if (output.name == 'fromDate') {
      this.creationStartDateTextBox.value = output.value;
      this.creationEndDateTextBox.minDate = dayjs(output.value).toDate();
    } else if (output.name == 'toDate') {
      this.creationEndDateTextBox.value = output.value;
    }
  }

  /**
  *applyFilter 
  */
  applyFilter(): void {
    this.filter.close();
    const payload: any = this. getFilterQuery();

    if (!Object.hasOwn(payload, 'orgnization')) {
      payload['orgnization'] = this.filterModal[0].selectedOption?.value?.toString();
    }
    this.getDashboardData(payload);
  }

  /**
  *resetFilter 
  */
  resetFilter(): void {
    this.filterModal[0] = {
      ...this.filterModal[0], selectedOption: ''
    };

    if (this.userDetail?.organizationDetail?.id) {
      this.filterForm.get('org').setValue(this.userDetail?.organizationDetail.id);
      this.filterModal[0] =
      {
        ...this.filterModal[0],
        selectedOption: { 'label': this.userDetail?.organizationDetail.name, 'value': this.userDetail?.organizationDetail.id },
      };
    }

    this.filterModal[1] = {
      ...this.filterModal[1], selectedOption: []
    };


    this.creationEndDateTextBox = { ...this.creationEndDateTextBox, value: dayjs().toISOString(), minDate: dayjs().subtract(1, 'year').toDate(), maxDate: dayjs().toDate() };
    this.creationStartDateTextBox = { ...this.creationStartDateTextBox, value: dayjs().subtract(6, 'month').toISOString(), maxDate: dayjs().toDate(), minDate: dayjs().subtract(1, 'year').toDate() };

    this.filterForm.reset();

  }

  /**
  * 
  * @returns 
  */
  getFilterQuery(): any {
    const filterPayLoad = {
    }

    if(this.filterDataLoaded) {

      const filterValue = this.filterForm?.value;
      Object.keys(filterValue).forEach((key) => {
        switch (key) {
        case 'org':
          if(filterValue.org) {
            filterPayLoad['orgnization'] = filterValue.org.toString();
          }
          break;
        
        case 'policyCoverType': 
          if(filterValue.policyCoverType) {
            filterPayLoad['policyType'] = filterValue.policyCoverType;
          }
          break;
        }
      });
      
      //creation start to end date range
      if(this.creationStartDateTextBox.value && this.creationEndDateTextBox.value) {
        const startDate = dayjs(this.creationStartDateTextBox.value as Date | string).format('YYYY-MM-DDT00:00:00');
        const endDate = dayjs(this.creationEndDateTextBox.value as Date | string).format('YYYY-MM-DDT00:00:00');
        filterPayLoad['startDate'] = startDate;
        filterPayLoad['endDate'] = endDate;
      }
    }

    return filterPayLoad;
  }

}

const FNOLStatusDetails = {
  'claimApprovalDetails': [
    {
      'claimStatusPercentage': 35,
      'claimStatusName': 'Approved'
    },
    {
      'claimStatusPercentage': 21,
      'claimStatusName': 'Received'
    },
    {
      'claimStatusPercentage': 16,
      'claimStatusName': 'Pending'
    },
    {
      'claimStatusPercentage': 28,
      'claimStatusName': 'Declined'
    }
  ]
}


const FNOLApprovedRejected = {
  'FNOLApprovalRejectionDetails': [
    {
      'fnolApprovalRejectionPercentage': 74,
      'fnolStatus': 'Approved'
    },
    {
      'fnolApprovalRejectionPercentage': 26,
      'fnolStatus': 'Rejected'
    }
  ]
}
