import { CommonModule } from '@angular/common';
import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { GoogleMapsModule } from '@angular/google-maps';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatIconModule } from '@angular/material/icon';
import { MatSidenavModule } from '@angular/material/sidenav';
import { Route, RouterModule } from '@angular/router';
import { MsalGuard } from '@azure/msal-angular';
import { TranslateModule } from '@ngx-translate/core';
import { DevExtremeModule, DxTagBoxModule } from 'devextreme-angular';
import { NgApexchartsModule } from 'ng-apexcharts';
import { ClaimGuard } from 'src/app/guards/claim-guard/claim.guard';
import { SharedModule } from 'src/app/shared/shared.module';
import { WidgetsModule } from 'src/app/widgets/widgets.module';
import { ClaimDashboardComponent } from './claim-dashboard/claim-dashboard.component';
import { ClaimListComponent } from './claim-list/claim-list.component';
import { CommonLayoutComponent } from './claim-management/common-layout/common-layout/common-layout.component';
import { PipeModule } from 'src/app/helper/pipe.module';

const claimRoutes: Route[] = [
  {
    path: '',
    canActivate: [ ClaimGuard ],
    component: ClaimDashboardComponent,
  },
  {
    path: 'my-claim',
    loadChildren: () =>
      import('../claim/claim-list/claim-list.module').then(
        m => m.ClaimListModule
      )
  },
  {
    path: 'claim-detail/:id',
    component: CommonLayoutComponent,
    canActivate: [ MsalGuard ],
    loadChildren: () =>
      import('../claim/claim-management/claim-management.module').then(
        m => m.ClaimManagementModule
      )
  },
  {
    path: 'garages',
    canActivate: [ MsalGuard ],
    loadChildren: () =>
      import('../claim/garages/garages.module').then(
        m => m.GaragesModule
      )
  },
]

@NgModule({
  declarations: [
    ClaimListComponent,
    ClaimDashboardComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(claimRoutes),
    MatSidenavModule,
    FormsModule,
    ReactiveFormsModule,
    CommonModule,
    SharedModule,
    MatIconModule,
    WidgetsModule, 
    NgApexchartsModule,
    MatExpansionModule,
    TranslateModule,
    DxTagBoxModule,
    DevExtremeModule,
    GoogleMapsModule,
    PipeModule
  ],
  exports: [
  ],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ]
})
export class ClaimsModule { }